<template>
  <v-container fluid fill-height style="padding:0;" id="fetch" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="Headers">Headers对象</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>Headers</code>对象专门用来处理请求头，常用的声明方式如下：</p>
                    <pre class="code">{{pres[0]}}</pre>
                    <p class=".body-1">其他相关操作如下：</p>
                    <pre class="code">{{pres[1]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="Request">Request对象</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>Request</code>用来自定义请求实例对象</p>
                    <pre class="code">{{pres[2]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="Response">Response对象</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1"><code>Response</code>用来自定义响应实例对象</p>
                    <pre class="code">{{pres[3]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    1. 空
        const header = new Headers();
        header.append('Content-Type', 'application/x-png');

    2. 传入对象
        const header = new Headers({
            'Content-Type': 'application/x-png',
            'Accept-Charset' : 'utf-8'
        });

    3. 传入二维数组
        const header = new Headers([
            ['Content-Type', 'application/x-png']
        ]);

    4. 根据其他的header实例对象创建
        new Headers(header);
    `,
    `
    1. 添加
        header.append('Content-Type', 'application/x-png'); 

    2. 获取
        header.get('Content-Type'); 

    3. 删除
        header.delete('Content-Type'); 

    4. 获取遍历器
        header.entries(); 
        header.keys();
        header.values();
    
    5. 是否包含
        header.has('Content-Type'); 

    6. 修改
        header.set('Content-Type', 'image/jpeg'); 
    `,
    `
    1. get请求
        const request = new Request('./ypos.jpg', {
            mode: 'same-origin',
            method: 'GET',
            cache: "no-cache",
            credentials: 'same-origin',
            headers: new Headers({
                'Content-Type': 'application/x-png',
                'Accept-Charset' : 'utf-8'
            })
        });

        fetch(request).then(res => res.blob()).then(res => {
            const url = URL.createObjectURL(res);
            // ...
        })

    2. post请求
        const request = new Request('./test', {
            method: 'POST',
            body: JSON.stringify({})
        });
    `,
    `
    1. 创建自定义的Response对象
        const response = new Response(new Blob(), {
            "status" : 200 , 
            "statusText" : "ok!"
        });

    2. 指定Response实例对象读取数据方法
        fetch('./ypos.jpg').then(res => {
            // res就是Response实例对象

            // res.arrayBuffer(); 将资源转换成buffer数组
            // res.blob(); 将资源转换成Blob对象
            // res.formData(); 转换成表单数据
            // res.json(); 转换成json数据
            // res.text(); 转换成文本数据

            if(response.ok){
                return res.blob();
            }else{
                throw new Error('HTTP error, status = ' + response.status);
            }
        }).then(res => {
            const objectURL = URL.createObjectURL(res);
            // ...
        })
    `
],
      time: "2019-07-25",
      title: "Fetch",
      items: [
        {
          title: "Headers对象",
          target: "#Headers"
        },
        { divider: true },
        {
          title: "Request对象",
          target: "#Request"
        },
        { divider: true },
        {
          title: "Response对象",
          target: "#Response"
        }
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#fetch {
  .blogcontent {
    padding-top: 20px;
  }
  .code {
    font-size: 12px;
    background-color: #f9f9f5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
</style>

